4c-.58-.23-1.21.22-1.21.85z"                                        fill="currentColor"></path>                                </svg>                            </NIcon>                        </n-button>                        <n-button disabled @click="muted" v-else text style="font-size: 24px">                            <NIcon>                                <svg xmlns="http://www.w3.org/2000/svg"                                     xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 24 24">                                    <path                                        d="M3.63 3.63a.996.996 0 0 0 0 1.41L7.29 8.7L7 9H4c-.55 0-1 .45-1 1v4c0 .55.45 1 1 1h3l3.29 3.29c.63.63 1.71.18 1.71-.71v-4.17l4.18 4.18c-.49.37-1.02.68-1.6.91c-.36.15-.58.53-.58.92c0 .72.73 1.18 1.39.91c.8-.33 1.55-.77 2.22-1.31l1.34 1.34a.996.996 0 1 0 1.41-1.41L5.05 3.63c-.39-.39-1.02-.39-1.42 0zM19 12c0 .82-.15 1.61-.41 2.34l1.53 1.53c.56-1.17.88-2.48.88-3.87c0-3.83-2.4-7.11-5.78-8.4c-.59-.23-1.22.23-1.22.86v.19c0 .38.25.71.61.85C17.18 6.54 19 9.06 19 12zm-8.71-6.29l-.17.17L12 7.76V6.41c0-.89-1.08-1.33-1.71-.7zM16.5 12A4.5 4.5 0 0 0 14 7.97v1.79l2.48 2.48c.01-.08.02-.16.02-.24z"                                        fill="currentColor"></path>                                </svg>                            </NIcon>                        </n-button>                    </template>                    <n-space style="height: 100px; justify-content: center">                        <n-slider v-model:value="sound" vertical @update:value="updateSound"/>                    </n-space>                </n-popover>                <n-button @click="active = true" text style="font-size: 24px">                    <NIcon>                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"                             viewBox="0 0 512 512">                            <path fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round"                                  stroke-width="48" d="M160 144h288"></path>                            <path fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round"                                  stroke-width="48" d="M160 256h288"></path>                            <path fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round"                                  stroke-width="48" d="M160 368h288"></path>                            <circle cx="80" cy="144" r="16" fill="none" stroke="currentColor" stroke-linecap="round"                                    stroke-linejoin="round" stroke-width="32"></circle>                            <circle cx="80" cy="256" r="16" fill="none" stroke="currentColor" stroke-linecap="round"                                    stroke-linejoin="round" stroke-width="32"></circle>                            <circle cx="80" cy="368" r="16" fill="none" stroke="currentColor" stroke-linecap="round"                                    stroke-linejoin="round" stroke-width="32"></circle>                        </svg>                    </NIcon>                </n-button>            </div>            <div v-else class="right">                <n-popover trigger="hover">                    <template #trigger>                        <n-button @click="muted" v-if="sound > 0" text style="font-size: 24px">                            <NIcon>                                <svg xmlns="http://www.w3.org/2000/svg"                                     xmlns:xlink="http://www.w3.org/1999/xlink"                                     viewBox="0 0 24 24">                                    <path                                        d="M3 10v4c0 .55.45 1 1 1h3l3.29 3.29c.63.63 1.71.18 1.71-.71V6.41c0-.89-1.08-1.34-1.71-.71L7 9H4c-.55 0-1 .45-1 1zm13.5 2A4.5 4.5 0 0 0 14 7.97v8.05c1.48-.73 2.5-2.25 2.5-4.02zM14 4.45v.2c0 .38.25.71.6.85C17.18 6.53 19 9.06 19 12s-1.82 5.47-4.4 6.5c-.36.14-.6.47-.6.85v.2c0 .63.63 1.07 1.21.85C18.6 19.11 21 15.84 21 12s-2.4-7.11-5.79-8.4c-.58-.23-1.21.22-1.21.85z"                                        fill="currentColor"></path>                                </svg>                            </NIcon>                        </n-button>                        <n-button @click="muted" v-else text style="font-size: 24px">                            <NIcon>                                <svg xmlns="http://www.w3.org/2000/svg"                                     xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 24 24">                                    <path                                        d="M3.63 3.63a.996.996 0 0 0 0 1.41L7.29 8.7L7 9H4c-.55 0-1 .45-1 1v4c0 .55.45 1 1 1h3l3.29 3.29c.63.63 1.71.18 1.71-.71v-4.17l4.18 4.18c-.49.37-1.02.68-1.6.91c-.36.15-.58.53-.58.92c0 .72.73 1.18 1.39.91c.8-.33 1.55-.77 2.22-1.31l1.34 1.34a.996.996 0 1 0 1.41-1.41L5.05 3.63c-.39-.39-1.02-.39-1.42 0zM19 12c0 .82-.15 1.61-.41 2.34l1.53 1.53c.56-1.17.88-2.48.88-3.87c0-3.83-2.4-7.11-5.78-8.4c-.59-.23-1.22.23-1.22.86v.19c0 .38.25.71.61.85C17.18 6.54 19 9.06 19 12zm-8.71-6.29l-.17.17L12 7.76V6.41c0-.89-1.08-1.33-1.71-.7zM16.5 12A4.5 4.5 0 0 0 14 7.97v1.79l2.48 2.48c.01-.08.02-.16.02-.24z"                                        fill="currentColor"></path>                                </svg>                            </NIcon>                        </n-button>                    </template>                    <n-space style="height: 100px; justify-content: center">                        <n-slider v-model:value="sound" vertical @update:value="updateSound"/>                    </n-space>                </n-popover>                <n-button @click="active = true" text style="font-size: 24px">                    <NIcon>                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"                             viewBox="0 0 512 512">                            <path fill="none" stroke="currentColor" stroke-linecap="round"                                  stroke-linejoin="round"                                  stroke-width="48" d="M160 144h288"></path>                            <path fill="none" stroke="currentColor" stroke-linecap="round"                                  stroke-linejoin="round"                                  stroke-width="48" d="M160 256h288"></path>                            <path fill="none" stroke="currentColor" stroke-linecap="round"                                  stroke-linejoin="round"                                  stroke-width="48" d="M160 368h288"></path>                            <circle cx="80" cy="144" r="16" fill="none" stroke="currentColor"                                    stroke-linecap="round"                                    stroke-linejoin="round" stroke-width="32"></circle>                            <circle cx="80" cy="256" r="16" fill="none" stroke="currentColor"                                    stroke-linecap="round"                                    stroke-linejoin="round" stroke-width="32"></circle>                            <circle cx="80" cy="368" r="16" fill="none" stroke="currentColor"                                    stroke-linecap="round"                                    stroke-linejoin="round" stroke-width="32"></circle>                        </svg>                    </NIcon>                </n-button>            </div>        </div>        <n-drawer v-model:show="active" :width="502" placement="right">            <n-drawer-content body-content-style="padding: 0;" :native-scrollbar="false">                <template #header>                    <div class="drawer-header">                        <n-space justify="space-between">                            <span>共{{ playStore.playList.length }}首</span>                            <n-button @click="reset" text type="primary">清空列表</n-button>                        </n-space>                    </div>                </template>                <n-table striped :bordered="false">                    <tbody>                    <tr @click="play(item)" v-for="item in playStore.playList" :key="item.id">                        <td>                            <n-icon v-if="playStore.playing.id === item.id && playStore.isPause">                                <svg xmlns="http://www.w3.org/2000/svg"                                     xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 24 24">                                    <path d="M7 9v6h4l5 5V4l-5 5H7z" fill="currentColor"></path>                                </svg>                            </n-icon>                            <n-icon color="#18a058" v-if="playStore.playing.id === item.id && playStore.isPause === false">                                <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"                                     viewBox="0 0 24 24">                                    <path                                        d="M3 9v6h4l5 5V4L7 9H3zm13.5 3A4.5 4.5 0 0 0 14 7.97v8.05c1.48-.73 2.5-2.25 2.5-4.02zM14 3.23v2.06c2.89.86 5 3.54 5 6.71s-2.11 5.85-5 6.71v2.06c4.01-.91 7-4.49 7-8.77s-2.99-7.86-7-8.77z"                                        fill="currentColor"></path>                                </svg>                            </n-icon>                            {{ item.name }}                        </td>                        <td>                            <span v-for="(singer, index) in item.singers">                                <span v-if="index !== 0"> / </span><span>{{ singer.name }}</span>                            </span>                        </td>                        <td>{{ item.time }}</td>                    </tr>                    </tbody>                </n-table>            </n-drawer-content>        </n-drawer>    </n-layout-footer>    <SongDetail/></template><script>import notImage from '../assets/notImage.jpg'import {HeartSharp} from '@vicons/ionicons5'import {    NIcon,    NButton,    NSpace,    NLayoutFooter,    NDrawer,    NDrawerContent,    NSlider,    NPopover,    NTable,    NImage,    useMessage,} from 'naive-ui'import {ref, getCurrentInstance, onMounted} from "vue";import {usePlayStore} from '../store'import SongDetail from './SongDetail'export default {    name: 'Audio',    setup() {        let active = ref(false)        let value = ref(0)        let audio = null;        let currentTime = ref('0:00')        let duration = ref('0:00')        let sound = ref(0)        let preSound = ref(0)        let message = useMessage()        const playStore = usePlayStore();        onMounted(() => {            audio = getCurrentInstance().refs.audio            playStore.audio = audio            playStore.isPause = audio.paused        })        function player() {            audio.play()            playStore.isPause = false        }        function pause() {            audio.pause()            playStore.isPause = true        }        function timeUpdate() {            // 获取当前播放了多少 百分比            value.value = audio.currentTime / audio.duration * 100            let second = Math.floor(audio.currentTime % 60)            if (second.toString().length < 2) {                second = '0' + second            }            currentTime.value = Math.floor(audio.currentTime / 60) + ':' + second        }        function canPlay() {            let totalTime = audio.duration            let second = Math.floor(totalTime % 60)            if (second.toString().length < 2) {                second = '0' + second            }            duration.value = Math.floor(totalTime / 60) + ':' + second            sound.value = audio.volume * 100        }        function update() {            audio.currentTime = value.value / 100 * audio.duration        }        function updateSound() {            audio.volume = sound.value / 100        }        function muted() {            if (audio.muted) {                sound.value = preSound.value            } else {                preSound.value = sound.value                sound.value = 0            }            audio.muted = !audio.muted        }        function play(item) {            playStore.play(playStore.playList, item)            playStore.isPause = false        }        function preSong() {            playStore.preSong()        }        function nextSong() {            playStore.nextSong()        }        function reset() {            value.value = 0            currentTime.value = '0:00'            duration.value = '0:00'            playStore.reset()        }        function showSongDetail(){            let songDetail = document.getElementById('song-detail')            if(songDetail.classList.contains('active-song-detail')){                songDetail.classList.remove('active-song-detail')            }else {                songDetail.className += ' active-song-detail'            }        }        function add(){            try {                playStore.add(playStore.playing.id)                playStore.playing.isLove = true                message.success('收藏成功')            } catch (e) {                message.error('收藏失败')            }        }        function remove(){            try {                playStore.remove(playStore.playing.id)                playStore.playing.isLove = false                message.success('取消收藏成功')            } catch (e) {                message.error('取消收藏失败')            }        }        return {            active,            value,            currentTime,            duration,            sound,            playStore,            player,            pause,            timeUpdate,            canPlay,            update,            updateSound,            muted,            play,            preSong,            nextSong,            reset,            showSongDetail,            add,            remove,            notImage,            HeartSharp,        };    },    components: {        SongDetail,        NIcon,        NSpace,        NLayoutFooter,        NButton,        NDrawer,        NDrawerContent,        NSlider,        NPopover,        NTable,        NImage,    }}</script><style scoped>.audio {    display: flex;    padding: 10px;}.left {    display: flex;    justify-content: flex-start;    flex: 1;}.left .song-detail {    display: flex;    flex-direction: column;    margin-top: 6px;    padding: 0 10px;}.middle {    flex: 3;    display: flex;    flex-direction: column;    justify-content: space-around;}.middle .option {    display: flex;    justify-content: space-between;    padding: 0 20px;}.right {    flex: 1;    display: flex;    align-items: center;    justify-content: flex-end;}.right .n-icon {    padding: 0 8px;}.progress {    display: flex;    align-items: center;    position: relative;}.progress span {    padding: 0 10px;}.drawer-header {    display: flex;    justify-content: space-between;}.n-table tr {    cursor: pointer;}</style>